<template>
  <input :value="modelValue" @input="updateInput" class="input" type="text">
</template>

<script>
  export default {
    name: 'my-input',
    props: {
      modelValue: [String, Number]
    },
    methods: {
      updateInput(event) {
        this.$emit('update:modelValue', event.target.value)
      }
    }
  }
</script>

<style scoped>
.input {
  margin-top: 20px;
  padding: 10px 15px;
  border: 1px solid #34495E;
  width: 100%;
}
</style>